.desktop-4,
.frame-icon52 {
  position: relative;
  overflow: hidden;
}
.frame-icon52 {
  height: 160px;
  width: 246px;
  flex-shrink: 0;
}
.desktop-4 {
  width: 100%;
  height: 1024px;
  background: linear-gradient(
      -59.31deg,
      rgba(14, 178, 211, 0),
      rgba(101, 177, 208, 0.2) 80.39%
    ),
    #fff;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-433xl) var(--padding-xl) var(--padding-393xl);
  box-sizing: border-box;
  line-height: normal;
  letter-spacing: normal;
  cursor: pointer;
}
